import React from 'react';
import { RadioProps } from '../Radio/Radio';
import styles from './RadioGroup.module.scss';

interface RadioGroupProps
  extends Pick<RadioProps, 'name' | 'variant' | 'iconVariant' | 'iconVariantChecked' | 'iconFontSize' | 'value'> {
  children: React.ReactNode;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  onChange: (value: any) => any;
}
/**
 * @deprecated Please do not use this component. Use <Radio /> directly instead
 */
function RadioGroup({
  children,
  name,
  variant = 'default',
  iconVariant,
  iconVariantChecked,
  iconFontSize,
  value,
  onChange,
}: RadioGroupProps) {
  if (process.env.NODE_ENV === 'development') {
    // eslint-disable-next-line
    console.warn('Deprecation warning: Please do not use RadioGroup. Use <Radio /> directly');
  }

  return (
    <div>
      {React.Children.map(children, child => {
        if (!React.isValidElement(child)) {
          return null;
        }

        return (
          <span className={styles[`${variant}-wrapper`]}>
            {React.cloneElement(child, {
              name,
              variant,
              iconVariant,
              iconVariantChecked,
              iconFontSize,
              checked: value === child.props.value,
              onChange: () => {
                onChange(child.props.value);
              },
            })}
          </span>
        );
      })}
    </div>
  );
}

export default RadioGroup;
