import React, { ChangeEventHandler, MouseEventHandler, ReactNode, forwardRef } from 'react';
import dbounce from 'lodash.debounce';
import Icon, { IconProps } from '../../Icon/Icon';
import icons from '../../Icon/icons';
import styles from './InputBase.module.scss';

// React releases an event after first function call. We don't want this if we have a debounced call
// In order to tell React to let the event persist, we call e.persist
// See https://reactjs.org/docs/events.html#event-pooling
function persist(fn: ChangeEventHandler<HTMLInputElement>): typeof fn {
  return function(event) {
    event.persist();

    return fn(event);
  };
}

export interface InputBaseProps extends React.InputHTMLAttributes<HTMLInputElement> {
  value?: string | number;
  error?: ReactNode;
  touched?: boolean;
  isValid?: boolean;
  debounce?: number;
  placeholder?: string;
  placeholderRight?: boolean;
  /** Keep placeholderRight visible even if input has text */
  placeholderAlwaysVisible?: boolean;

  /** The name of the icon */
  icon?: keyof typeof icons;
  /** Whether the icon should display as black or white */
  iconVariant?: 'primary' | 'alternate'; // primary == variant 1 == white icon. alternate == variant 0 == dark icon
  iconProps?: Partial<IconProps>;
  onIconClick?: MouseEventHandler<HTMLButtonElement>;
}

const InputBase = forwardRef<HTMLInputElement, InputBaseProps>(
  (
    {
      type,
      error,
      touched,
      isValid,
      debounce,
      onChange,
      placeholder,
      placeholderRight,
      placeholderAlwaysVisible,
      disabled,
      id,
      icon,
      iconVariant = 'primary',
      iconProps,
      onIconClick,
      ...inputProps
    },
    ref
  ) => {
    InputBase.displayName = 'InputBase';

    const handleChange = debounce && onChange ? persist(dbounce(onChange, debounce)) : onChange;
    const inputClasses = [
      styles['input'],
      placeholderRight && styles['input-placeholder-right'],
      disabled && styles['input-disabled'],
    ]
      .filter(Boolean)
      .join(' ');

    const inputLineClasses = !disabled
      ? [
          styles['input-line'],
          touched && error && styles['input-line-error'],
          touched && isValid && styles['input-line-valid'],
          touched && styles['input-line-touched'],
        ]
          .filter(Boolean)
          .join(' ')
      : '';

    const iconWrapperClass = [styles['icon-wrapper'], iconVariant === 'alternate' && styles['icon-wrapper-white']]
      .filter(Boolean)
      .join(' ');

    return (
      <div className={styles['input-wrapper']}>
        <input
          {...inputProps}
          className={inputClasses}
          type={type}
          onChange={handleChange}
          placeholder={placeholderAlwaysVisible ? '' : placeholder}
          disabled={disabled}
          id={id}
          ref={ref}
        />
        {placeholderAlwaysVisible && <span className={styles['right-placeholder']}>{placeholder}</span>}
        <div className={inputLineClasses} />
        {icon && (
          <button className={iconWrapperClass} onClick={onIconClick} type="button">
            <Icon variant={iconVariant === 'primary' ? 1 : 0} name={icon} fontSize="16px" {...iconProps} />
          </button>
        )}
      </div>
    );
  }
);

export default InputBase;
