import React, { forwardRef, useState } from 'react';
import Autosuggest from 'react-autosuggest';
import InputBase, { InputBaseProps } from '../InputBase/InputBase';
import styles from './AutoSuggestInput.module.scss';

type Part = {
  start: number;
  end: number;
};
export function findParts(input: string, text: string): Part[] {
  if (!input || !text) {
    return [];
  }

  // Look for case insensitive ('gi' flag) matches
  const regex = new RegExp(input, 'gi');
  const chunks: Part[] = [];
  let match;

  while ((match = regex.exec(text))) {
    const start = match.index;
    const end = regex.lastIndex;

    // We do not return zero-length matches
    if (end > start) {
      chunks.push({ start, end });
    }

    // Prevent browsers like Firefox from getting stuck in an infinite loop
    // See http://www.regexguru.com/2008/04/watch-out-for-zero-length-matches/
    if (match.index === regex.lastIndex) {
      regex.lastIndex++;
    }
  }

  return chunks;
}

export function highlightInput(input: string, text: string) {
  const parts = findParts(input, text);

  if (parts.length === 0) {
    return text;
  }

  // Where to start boldifying
  let last = 0;

  return parts.map(({ start, end }, index) => {
    const jsx = (
      <>
        {text.slice(last, start)}
        <strong>{text.slice(start, end)}</strong>
        {index === parts.length - 1 && text.slice(end) /* Render the rest if it's the last chunk */}
      </>
    );

    last = end;

    return jsx;
  });
}

export interface AutoSuggestInputProps extends InputBaseProps {
  suggestions: {
    [s: string]: unknown;
    /** The string representation of the value */
    value: string;
  }[];
  onSuggestionSelected?: (v: AutoSuggestInputProps['suggestions'][number]) => void;
  onSuggestionHighlighted?: (v: AutoSuggestInputProps['suggestions'][number]) => void;
}
const AutoSuggestInput = forwardRef<Autosuggest, AutoSuggestInputProps>(
  (
    {
      suggestions,
      onSuggestionSelected,
      onSuggestionHighlighted,

      value = '',
      onChange = () => {},

      ...inputProps
    },
    ref
  ) => {
    AutoSuggestInput.displayName = 'AutoSuggestInput';

    const [filteredSuggestions, setFilteredSuggestions] = useState(suggestions);

    return (
      <Autosuggest
        ref={ref}
        suggestions={filteredSuggestions}
        onSuggestionsFetchRequested={({ value }) => {
          const v = value.toLowerCase();

          setFilteredSuggestions(
            suggestions.filter(s => {
              return s.value
                .toString()
                .toLowerCase()
                .includes(v);
            })
          );
        }}
        onSuggestionsClearRequested={() => setFilteredSuggestions([])}
        getSuggestionValue={s => s.value}
        renderSuggestion={(s, { query }) => highlightInput(query, s.value)}
        inputProps={{
          onChange,
          value: value.toString(),
        }}
        // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
        // @ts-ignore: The typing for the onChange and onBlur from the Autosuggest.InputProps is not correct :(
        renderInputComponent={autoSuggestInputProps => <InputBase {...inputProps} {...autoSuggestInputProps} />}
        onSuggestionSelected={
          typeof onSuggestionSelected === 'function'
            ? (event, { suggestion }) => {
                event.stopPropagation();
                event.preventDefault();

                onSuggestionSelected(suggestion);
              }
            : undefined
        }
        onSuggestionHighlighted={
          typeof onSuggestionHighlighted === 'function'
            ? stuff => onSuggestionHighlighted(stuff.suggestion.value)
            : undefined
        }
        shouldRenderSuggestions={() => true}
        theme={{
          container: styles['container'],
          suggestionsContainer: styles['suggestionsContainer'],
          suggestionsList: styles['suggestionsList'],
          suggestion: styles['suggestion'],
          suggestionHighlighted: styles['suggestionHighlighted'],
        }}
      />
    );
  }
);

export default AutoSuggestInput;
