import * as React from 'react';
import styles from './BoxShadow.module.scss';

interface Props {
  children?: React.ReactNode;
  display?: string;
  small?: boolean;
  className?: string;
  dark?: boolean;
}

class BoxShadow extends React.PureComponent<Props> {
  render() {
    const { children, display, className, small, dark, ...props } = this.props;

    const sty = {};

    if (display) {
      sty['display'] = display;
    }

    return (
      <div
        className={[
          styles['box-shadow'],
          dark && styles['box-shadow-dark'],
          small && styles['box-shadow-small'],
          !small && styles['box-shadow-default'],

          className,
        ]
          .filter(Boolean)
          .join(' ')}
        style={sty}
        {...props}
      >
        {children}
      </div>
    );
  }
}

export default BoxShadow;
