import React, { useState, useEffect, useRef, useCallback } from 'react';
import { mergeClassNames } from '../../utils/helpers';
import Item from './Item';
import styles from './Menu.module.scss';
import { MenuItem } from './Menu.types';

interface MenuProps {
  className: string;
  menuItems: MenuItem[];
  isActiveItem(item: MenuItem): boolean;
  renderItem(activeClassName: string, item: MenuItem): React.ReactNode;
}

function Menu({ className, menuItems, isActiveItem, renderItem, ...props }: MenuProps) {
  const navRef = useRef<HTMLUListElement | null>(null);
  const [isStartShadowVisible, setStartShadowVisible] = useState(false);
  const [isEndShadowVisible, setEndShadowVisible] = useState(false);

  const setShadows = useCallback(() => {
    const nav = navRef.current;

    if (!nav) {
      return;
    }

    const isStartShadowVisible = nav.scrollLeft > 1;
    const isEndShadowVisible = nav.scrollLeft < nav.scrollWidth - nav.offsetWidth;

    setStartShadowVisible(isStartShadowVisible);
    setEndShadowVisible(isEndShadowVisible);
  }, []);

  const handleScroll = useCallback(() => {
    setShadows();
  }, [setShadows]);

  // READ THIS FIRST! https://reactjs.org/docs/hooks-faq.html#how-can-i-measure-a-dom-node
  const handleRef = useCallback((ulElement: null | HTMLUListElement) => {
    if (ulElement === null) {
      return;
    }

    // Initializes navRef which is used in setShadows and setFocus
    navRef.current = ulElement;
  }, []);

  useEffect(() => {
    window.addEventListener('resize', handleScroll);

    return () => window.removeEventListener('resize', handleScroll);
  }, [handleScroll]);

  const menuClassNames = mergeClassNames(styles['Menu'], [
    isStartShadowVisible && styles['Menu--startShadow'],
    isEndShadowVisible && styles['Menu--endShadow'],
    className,
  ]);

  return (
    <nav className={menuClassNames} {...props}>
      <ul className={styles['Menu-nav']} onScroll={handleScroll} ref={handleRef}>
        {menuItems.map(item => {
          const isActive = isActiveItem(item);

          return (
            <Item
              key={`menu-${item.url}`}
              isActive={isActive}
              amount={menuItems.length}
              item={item}
              onScrollIntoView={setShadows}
              render={renderItem}
            />
          );
        })}
      </ul>
    </nav>
  );
}

export default Menu;
