import React from 'react';
import ReactDatePicker, { ReactDatePickerProps } from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import Input from '../Input';
import { InputProps } from '../Input/Input';
import styles from './DatePicker.module.scss';

interface DatePickerProps extends ReactDatePickerProps {
  className?: string;
  inputProps?: InputProps;
}

const DatePicker = (props: DatePickerProps) => {
  const ref = React.useRef<ReactDatePicker>(null);
  const calendarClassName = [styles.Calendar, props.className].filter(Boolean).join(' ');

  return (
    <ReactDatePicker
      customInput={
        <Input
          iconProps={{ fontSize: '25px' }}
          icon="calendar"
          iconVariant="alternate"
          onIconClick={() => ref.current && ref.current.setOpen(true)}
          {...props.inputProps}
        />
      }
      calendarClassName={calendarClassName}
      ref={ref}
      // all props are overwritable
      {...props}
    />
  );
};

export default DatePicker;
