class Icon extends React.PureComponent<IconProps> {
  static defaultProps = {
    fontSize: '150%',
    variant: 0,
    className: '',
    pngonly: undefined,
    name: '',
  };

  getVariantColor() {
    const { variant } = this.props;
    let color;

    switch (variant) {
      case 0:
        color = 'charcoal-gray';
        break;

      case 1:
        color = 'white';
        break;

      case 2:
        color = 'bermuda-gray';
        break;

      case 3:
        color = 'warm-gray';
        break;

      case 4:
        color = 'silver';
        break;

      default:
        color = 'charcoal-gray';
        break;
    }

    return styles[`u-font-color-${color}`];
  }

  render() {
    const {
      className,
      fontSize,
      name,
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      variant,

      ...props
    } = this.props;

    const style = { fontSize: fontSize };
    const IconComponent = Icons[iconMap[name] ? iconMap[name] : name];

    if (!IconComponent) {
      // eslint-disable-next-line
      console.error(`[Windmolen]: Can't find icon "${name}"`);

      return null;
    }

    if (iconMap[name] && process.env.NODE_ENV === 'development') {
      // eslint-disable-next-line
      console.warn(`[Windmolen]: ${name} will be deprecated, use ${iconMap[name]} instead.`);
    }

    return (
      <IconComponent
        className={[styles['icon'], this.getVariantColor(), className].filter(Boolean).join(' ')}
        style={style}
        {...props}
      />
    );
  }
}

export default Icon;
