import * as React from 'react';
import { mergeClassNames } from '../../utils/helpers';
import styles from './HighlightedHeader.module.scss';

export interface HighlightedHeaderProps extends React.HTMLAttributes<HTMLDivElement> {
  children?: React.ReactNode;
  className?: string;
}

class HighlightedHeader extends React.PureComponent<HighlightedHeaderProps> {
  render() {
    const { className, children, ...props } = this.props;

    return (
      <div className={mergeClassNames(styles['HighlightedHeader'], [className])} {...props}>
        {children}
      </div>
    );
  }
}

export default HighlightedHeader;
