import React, { useEffect, ReactNode, useState, CSSProperties } from 'react';
import Icon from '../Icon';
import Pressable from '../Pressable';
import Portal from '../Portal';
import ModalContent from './ModalContent';
import ModalHeader from './ModalHeader';
import styles from './Modal.module.scss';

const BACKDROP_ID = 'modal-backdrop';

function addNoScroll() {
  if (!document.body || !document.body.classList) {
    return;
  }

  if (typeof document.body.classList.add === 'function') {
    document.body.classList.add(styles['no-scroll']);
  }
}

function removeNoScroll() {
  if (!document.body || !document.body.classList) {
    return;
  }

  if (typeof document.body.classList.remove === 'function') {
    document.body.classList.remove(styles['no-scroll']);
  }
}

interface ModalProps {
  children: ReactNode;
  className?: string;
  style?: CSSProperties;
  isOpen?: boolean;
  allowBodyScroll?: boolean;
  closeOnBackdropClick?: boolean;
  onClose?: () => void;
  locationInDom?: HTMLElement;
}
function Modal({
  children,
  className,
  style,
  isOpen: isOpenControlled,
  allowBodyScroll = false,
  closeOnBackdropClick = false,
  onClose = () => {},
  locationInDom,
}: ModalProps) {
  const [isOpen, setIsOpen] = useState(true);

  // If the component is controlled, we don't watch the local `isOpen` state
  const isControlled = isOpenControlled !== undefined;
  const modalIsOpen = isControlled ? isOpenControlled : isOpen;

  useEffect(() => {
    function handleEscape({ key }: KeyboardEvent) {
      if (key === 'Esc' || key === 'Escape') {
        if (!isControlled) {
          setIsOpen(false);
        }

        onClose();
      }
    }

    if (modalIsOpen) {
      document.addEventListener('keydown', handleEscape);
    }

    return () => {
      document.removeEventListener('keydown', handleEscape);
    };
  }, [isOpenControlled, onClose, modalIsOpen, isControlled]);

  useEffect(() => {
    if (modalIsOpen && !allowBodyScroll) {
      addNoScroll();
    } else {
      removeNoScroll();
    }

    // Always remove it in case the component unmounts unexpectedly
    return () => {
      removeNoScroll();
    };
  }, [modalIsOpen, allowBodyScroll]);

  function handleClose() {
    if (!isControlled) {
      setIsOpen(false);
    }

    onClose();
  }

  function onBackdropClick(event: React.MouseEvent<HTMLDivElement>) {
    if (!closeOnBackdropClick) {
      return;
    }

    // Make sure that if you have a modal in a modal it won't close all modals on backdrop click, but rather only the top one.
    event.stopPropagation();

    // @ts-ignore: id exists, just not on the type for some reason
    if (event.target && event.target.id === BACKDROP_ID) {
      handleClose();
    }
  }

  if (!modalIsOpen) {
    return null;
  }

  return (
    <Portal node={locationInDom}>
      <div className={styles['modal-backdrop']} id={BACKDROP_ID} tabIndex={-1} role="dialog" onClick={onBackdropClick}>
        <div className={`${styles['modal']} ${className || ''}`} style={style} role="document">
          {children}
          <Pressable as="button" variant="text" onClick={handleClose} className={styles['close-button']}>
            <Icon name="close" />
          </Pressable>
        </div>
      </div>
    </Portal>
  );
}

Modal.Content = ModalContent;
Modal.Header = ModalHeader;

export default Modal;
