/* Omitting canvasRef and making it optional, see https://github.com/alampros/react-confetti/issues/72 for more info */
interface ConfettiProps extends Omit<ReactConfettiProps, 'canvasRef'> {
  /** How long the confetti should last. This gradually decreases the number of pieces over {easeOutMs} milliseconds */
  easeOutMs?: number;
  canvasRef?: ReactConfettiProps['canvasRef'];
}
function Confetti({ easeOutMs = 0, numberOfPieces = 200, ...props }: ConfettiProps) {
  const { innerHeight, innerWidth } = useWindowSize();
  const [howHardToParty, setHowHardToParty] = useState(numberOfPieces);

  useEffect(() => {
    let decrease: NodeJS.Timeout, decreaseFurther: NodeJS.Timeout, cancelPartyTime: NodeJS.Timeout;

    setHowHardToParty(numberOfPieces);

    if (easeOutMs > 0) {
      decrease = setTimeout(() => setHowHardToParty(numberOfPieces / 2), easeOutMs * 0.6);
      decreaseFurther = setTimeout(() => setHowHardToParty(numberOfPieces / 4), easeOutMs * 0.8);
      cancelPartyTime = setTimeout(() => setHowHardToParty(0), easeOutMs);
    }

    return () => {
      if (easeOutMs > 0) {
        clearTimeout(decrease);
        clearTimeout(decreaseFurther);
        clearTimeout(cancelPartyTime);
      }
    };
  }, [easeOutMs, numberOfPieces]);

  return (
    <Portal>
      {/* Not nice, but we need it because the React Confetti types are incorrect :( 
      // @ts-ignore */}
      <ReactConfetti
        style={{ position: 'fixed' }}
        height={innerHeight}
        width={innerWidth}
        numberOfPieces={howHardToParty}
        drawShape={
          supportsPath2D
            ? function(this: Particle, ctx) {
                const shape = paths[this.shape];

                if (shape) {
                  ctx.fill(shape);
                }
              }
            : undefined
        }
        {...props}
      />
    </Portal>
  );
}

export default Confetti;
