import * as React from 'react';
import { mergeClassNames } from '../../utils/helpers';
import Icon from '../Icon';
import icons from '../Icon/icons';
import styles from './Pressable.module.scss';
import { PressableProps } from './Pressable.types';

const renderIcon = (icon: keyof typeof icons, hideicon: boolean, iconSize: string) =>
  !hideicon && (
    <React.Fragment>
      <Icon
        name={icon}
        className={[styles['button-icon'], styles['icon-variant-1']].join(' ')}
        variant={1}
        fontSize={iconSize}
      />
      <Icon
        name={icon}
        className={[styles['button-icon'], styles['icon-variant-2']].join(' ')}
        variant={2}
        fontSize={iconSize}
      />
      <Icon
        name={icon}
        className={[styles['button-icon'], styles['icon-variant-3']].join(' ')}
        variant={3}
        fontSize={iconSize}
      />
    </React.Fragment>
  );

class Pressable extends React.PureComponent<PressableProps> {
  render() {
    const {
      children,
      className,
      variant,
      small: smallDeprecatedProp,
      iconplacement,
      icon,
      icononly,
      color,
      hideicon,
      size,
      iconSize,
      as,
      ...props
    } = this.props;

    const isXSmall = size === 'xsmall';
    const isSmall = size === 'small' || smallDeprecatedProp;
    let iconSizeValue;

    if (iconSize) {
      iconSizeValue = iconSize;
    } else {
      iconSizeValue = isSmall ? '11px' : '14px';
    }

    const shouldHideicon = variant === 'text' || !!hideicon;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const RenderedComponent: any = as;

    if (!variant) {
      return null;
    }

    if (smallDeprecatedProp && process.env.NODE_ENV === 'development') {
      // eslint-disable-next-line
      console.warn('The `small` prop in Button/Link has been deprecated since v4.9.0, use `{ size: "small" }` instead');
    }

    const classNames = mergeClassNames(styles.button, [
      styles[variant],
      isXSmall && styles['button-xsmall'],
      isSmall && styles['button-small'],
      styles[`button-${iconplacement}`],
      icononly && styles['button-icononly'],
      color && styles[color],
      className,
    ]);

    return (
      <RenderedComponent className={classNames} {...props}>
        {iconplacement === 'start' && icon && renderIcon(icon, shouldHideicon, iconSizeValue)}
        {!icononly && children}
        {iconplacement === 'end' && icon && renderIcon(icon, shouldHideicon, iconSizeValue)}
      </RenderedComponent>
    );
  }
}

export default Pressable;
