import * as React from 'react';
import debounce from 'lodash.debounce';
import icons from '../Icon/icons';
import Icon from '../Icon';
import styles from './TextArea.module.scss';

interface Props
  extends React.DetailedHTMLProps<React.TextareaHTMLAttributes<HTMLTextAreaElement>, HTMLTextAreaElement> {
  debounceTime?: number;
  onDebouncedChange?(value: string): void;
  icon?: keyof typeof icons;
}

interface State {
  isFocused: boolean;
}

export default class TextArea extends React.PureComponent<Props, State> {
  private emitChangeDebounced: (value: string) => void;

  state: State = {
    isFocused: false,
  };

  public constructor(props: Props) {
    super(props);

    this.emitChangeDebounced = debounce(this.emitChange, this.props.debounceTime || 250);
  }

  componentWillUnmount(): void {
    // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
    // @ts-ignore the debounce function from lodash isn't correctly typed
    this.emitChangeDebounced.cancel();
  }

  private handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    this.emitChangeDebounced(e.target.value);

    if (this.props.onChange) {
      this.props.onChange(e);
    }
  };

  private onTextAreaFocus = (): void => {
    this.setState({ isFocused: true });
  };

  private onTextAreaBlur = (): void => {
    this.setState({ isFocused: false });
  };

  render(): React.ReactNode {
    const { isFocused } = this.state;
    const { className, debounceTime, onDebouncedChange, icon, ...props } = this.props;

    return (
      <div className={[`${className || ''}`, styles['vdb-textarea-container']].join(' ')}>
        <textarea
          className={styles['vdb-textarea']}
          {...props}
          onChange={this.handleChange}
          onFocus={this.onTextAreaFocus}
          onBlur={this.onTextAreaBlur}
        />
        {icon ? (
          <Icon
            name={icon}
            className={isFocused ? styles['vdb-textarea-icon-focus'] : styles['vdb-textarea-icon']}
            fontSize="16"
          />
        ) : null}
      </div>
    );
  }

  private emitChange(value: string): void {
    if (this.props.onDebouncedChange) {
      this.props.onDebouncedChange(value);
    }
  }
}
