import * as React from 'react';
import ImageStack, { StackedImage } from '../ImageStack';
import Image from '../Image';
import Container from '../Container';
import Row from '../Row';
import Col from '../Col';
import Flex from '../Flex';
import styles from './ContentBlock.module.scss';

export interface Props {
  images: StackedImage[];
  className?: string;
  noShadow?: boolean; // Disable the shadow for the stacked images
  flipped?: boolean; // When specified; the content/image container will be flipped.
  imagesFlipped?: boolean; // When specified; the images will flip sides.
}
const ContentBlock: React.FC<Props> = ({ children, flipped, images, imagesFlipped, className, noShadow }) => {
  if (images.length > 1) {
    // Image stack layout
    return (
      <Container className={[styles['content-block'], className].filter(Boolean).join(' ')}>
        <Row alignItems="center">
          <div className={[styles['image-container'], flipped && styles['flipped']].filter(Boolean).join(' ')}>
            <ImageStack images={images} flipped={imagesFlipped} noShadow={noShadow} className={styles['image-stack']} />
          </div>
          <Col col={12} lg={5} lgOffset={flipped ? 7 : 0}>
            {children}
          </Col>
        </Row>
      </Container>
    );
  }

  return (
    <Flex alignItems="center" wrap="wrap" className={[styles['content-block'], className].filter(Boolean).join(' ')}>
      <div className={[styles['image-container'], flipped && styles['flipped']].filter(Boolean).join(' ')}>
        <Image
          {...images[0]}
          className={[styles['single-image'], noShadow && styles['no-shadow']].filter(Boolean).join(' ')}
        />
      </div>
      <Container>
        <Row>
          <Col col={12} lg={5} lgOffset={flipped ? 7 : 0}>
            {children}
          </Col>
        </Row>
      </Container>
    </Flex>
  );
};

export default ContentBlock;
