import React, { useState, CSSProperties } from 'react';
import { mergeClassNames } from '../../utils/helpers';
import Table from '../Table';
import Icon from '../Icon';
import { TableRowProps } from './types';
import styles from './CollapsibleTable.module.scss';

const { Row, Cell } = Table;

export function TableRow({ isUnderBreakpoint, cols, collapsibleRow }: TableRowProps) {
  const [isExpanded, toggleExpanded] = useState<boolean>(false);
  // the 'collapse' icon is off-center and needs to be corrected
  const iconStyle: CSSProperties = { textAlign: 'center', position: 'relative', top: isExpanded ? 5 : 0 };
  const collapsibleRowClass = mergeClassNames(styles['TableRow__Collapsible'], [
    isExpanded ? styles['TableRow__Collapsible--expanded'] : styles['TableRow__Collapsible--collapsed'],
  ]);

  return (
    <>
      <Row>
        {cols.map((renderFn, i) => (
          <Cell key={i}>{renderFn()}</Cell>
        ))}

        {isUnderBreakpoint && (
          <Cell onClick={() => toggleExpanded(!isExpanded)} className={styles.TableRow__Icon}>
            <div style={iconStyle}>
              <Icon name={isExpanded ? 'collapse' : 'add'} fontSize="13px" />
            </div>
          </Cell>
        )}
      </Row>

      {isUnderBreakpoint && (
        <Row>
          <Cell colSpan={cols.length + 1} className={collapsibleRowClass}>
            {collapsibleRow()}
          </Cell>
        </Row>
      )}
    </>
  );
}
