import React from 'react';
import Switch, { SwitchProps } from '../Switch/Switch';

export interface RadioProps extends Omit<SwitchProps, 'type'> {
  /** @deprecated Please do not use this prop. Use customIcon directly instead. Will be ignored if customIcon is present */
  customRadioIcon?: SwitchProps['customIcon'];
  /** @deprecated Please do not use this prop. Use customIconChecked directly instead. Will be ignored if customIconChecked is present */
  customRadioIconChecked?: SwitchProps['customIconChecked'];
}

function Radio({
  icon = 'radio-unchecked',
  iconChecked = 'radio-checked',
  customIcon,
  customIconChecked,
  customRadioIcon,
  customRadioIconChecked,
  ...props
}: RadioProps) {
  if (process.env.NODE_ENV === 'development' && (customRadioIcon || customRadioIconChecked)) {
    // eslint-disable-next-line
    console.warn(
      'Deprecation warning: Please do not use the props customRadioIcon and customRadioIconChecked. Instead, use the props customIcon and customIconChecked.'
    );
  }

  return (
    <Switch
      {...props}
      type="radio"
      icon={icon}
      iconChecked={iconChecked}
      customIcon={customIcon || customRadioIcon}
      customIconChecked={customIconChecked || customRadioIconChecked}
    />
  );
}

export default Radio;
